.card {
  display: flex;
  align-items: center;
  width: 300px;
  margin: 15px auto; 
  padding: 10px;
  border: 2px solid #000;
  border-radius: 12px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.4);
  background-color: #2b2b2b; 
  color: #fff;
  font-weight: bold;
  font-size: 1.3rem;
}

.card img {
  width: 60px;
  height: 60px;
  margin-right: 15px;
  border-radius: 8px;
}
